.gallery-container {
  // margin-bottom: 190px;
  // height: 720px;
  position: relative;
  background: #d83840;
  padding: 0;

  @include tablets {
    padding-top: 0px;
    width: 70%;
    margin: 0 auto;
  }

  @include phones {
    padding-top: 0px;
    height: auto;
    // background-color: #fff;
    width: 70%;
    margin: 0 auto;
    margin-bottom: 107px;
  }
}

.glide {
  position: relative;
  z-index: 1;

  &__img {
    height: 700px;
    object-fit: cover;
    // for IE
    transition: transform 0.2s linear;
    font-family: "object-fit: cover;";

    @include laptops {
      height: 450px;
    }

    @include tablets {
      height: 300px;
    }

    @include phones {
      height: 206px;
    }
  }

  &__slides {
    // margin-bottom: 25px;
    position: relative;
    // top: 25px;
  }

  &__arrow {
    width: 50px;
    height: 50px;
    background-color: $theme;
    background: #fff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 80px;
    margin-top: -29px;
    cursor: pointer;
    transition: 0.3s;

    @include tablets {
      width: 25px;
      height: 25px;
      top: 52%;
    }

    @include phones {
      width: 25px;
      height: 25px;
      margin-top: 0;
    }

    &:hover {
      background-color: #202139;
      opacity: 0.8;
      transition: 0.3s;
    }

    &:hover .glide__arrow-icon {
      color: #fff;
      transition: 0.3s;
    }

    &-icon {
      color: #2f322e;
      font-size: 40px;

      // @include tablets {
      //   font-size: 25px;
      // }

      // @include phones {
      //   font-size: 25px;
      // }
    }

    &--prev {
      right: 130px;
    }

    &--next {
      right: 62px;
    }
  }

  // &__bullet {
  //   width: 10px;
  //   height: 10px;
  //   border-radius: 50%;
  //   border: solid 1.5px #fff;
  //   margin: 0 5px;
  //   cursor: pointer;
  //   &--active {
  //     background-color: #fff;
  //   }
  // }

  // &__bullets {
