  //   top: 60px;

  //   @include tablets {
  //     top: 40px;
  //   }

  //   @include phones {
  //     top: 35px;
  //   }
  // }
}

.fa-angle-left {
  margin-left: -2px;
  @include phones {
    margin-left: -2px;
    margin-top: -1.5px;
  }
}

.fa-angle-right {
