@font-face {
  font-family: "Gotham";
  src: url("./fonts/gothambook-webfont.woff2") format("woff2"),
    url("./fonts/gothambook-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

